@import url('https://fonts.googleapis.com/css2?family=Raleway&family=Roboto+Slab:wght@700&display=swap');
:root {
  --black: #2c3e50;
  --light-black: #b2bec3;
  --main-transition: .4s
}

::-webkit-scrollbar {
  width: 20px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #2f2f2f;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #fc0363; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(124, 57, 57); 
}

.cursor {
  width: 30px;
  height: 30px;
  border: 3px solid #fc0363;
  border-radius: 50%;
  position: fixed;
  pointer-events: none;
  transition: border var(--main-transition), width var(--main-transition), height var(--main-transition);
  transform: translate(-50%, -50%);
  z-index: 999;
}
* {
  cursor: none !important;
}
.App {
  overflow-x: hidden;
  /* cursor: none; */
}
body { 
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}
ul {
  padding: 0;
  list-style: none;
}
h1, h2 {
  font-family: 'Roboto Slab', serif;
}
p {
  font-family: 'Raleway', sans-serif;
}

.container {
  max-width: 1170px;
  margin: auto;
}
.head {
  background: #ebf0ed;
}

.head .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 22px 0;
}
.head ul {
  display: flex;
  align-items: center;
  margin: 0;
}
.head img {
  width: 200px;
}
.head nav li {
  font-size: 17px;
  margin: 10px;
}
.head nav a {
  color: var(--black);
  transition: 0.4s;
}
nav ul:hover a {
  color: var(--light-black);
}
nav ul a:hover {
  color: var(--black);
}
nav ul a::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: #313030;
  transition: .4s;
}
nav ul a:hover::after {
  width: 100%;
}

.head-bottom, .section2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 34px 0;
}
.head-bottom img, .section2 img {
  width: 550px;
}
.head-bottom .left-info, .section2 .right-info {
  width: 45%;
}
.head-bottom h1 {
  font-size: 48px;  
}
.head-bottom p, .section2 .right-info {
  font-size: 19px;
}

.section1 ul {
  display: flex;
  justify-content: space-between;
  padding: 63px 0;
}
.section1 li {
  box-sizing: border-box;
  display: flex;
  flex-flow: column nowrap;
  font-size: 18px;
  font-weight: 600;
  text-align: center;
  width: 30%;
}

footer {
  background: #ebf0ed;
  padding: 39px 0 1px;
}
.footer-top {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 58px;
}
.footer-top ul {
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 23px;
  margin: 10px 0;
}
footer p a {
  color: var(--black);
  text-decoration: underline;
}
footer p a:hover {
  text-decoration: none;
}
.footer-top .social-links ul a {
  border: 10px solid transparent;
}
